<template>
  <div class="user">
    <!-- 个人信息 -->
    <div class="userInfo">
      <div class="headImg"><img :src="userInfo.avatar"></div>
      <div class="Profile">
        <p><span class="name">{{userInfo.nickname}}</span><van-icon name="setting-o" color="#fff" size="20" @click="goInfo"/></p>
        <div class="id"><span>ID：{{userInfo.uid}}</span><router-link to="/personalInfo" tag="i" class="iconfont iconbianji"></router-link></div>
      </div>
    </div>

    <!-- 余额 -->
    <ul class="myList">
      <router-link v-for="(item,i) in myList" :key="i" :to="item.url" tag="li">
          <van-icon :name="item.icon" size="30" color="#ff8600"/>
          <span class="text">{{item.title}}</span>
        </router-link>
    </ul>

    <!-- 我的订单 -->
    <div class="myOrder">
      <div class="top">
        <span class="text">我的订单</span>
        <router-link to="/order" tag="span" class="look">查看全部订单</router-link>
        <van-icon name="arrow" size="12px" color="#999"/>
      </div>
      <ul class="nav">
        <router-link tag="li" v-for="(item,i) in navList" :key="i" :to="item.url" >
          <div class="icon">
            <i :class="item.icon"></i>
            <div class="badge" v-if="item.count">{{item.count}}</div>
          </div>
          <span class="text">{{item.title}}</span>
        </router-link>
      </ul>
    </div>

    <!-- 我的服务 -->
    <!-- <div class="myService">
      <div class="top">
        <span class="text">我的服务</span>
      </div>
      <ul class="nav">
        
      </ul>
    </div> -->

  </div>
</template>

<script>
import "../assets/style/user.less"
export default {
  name:"user",
  data(){
    return{
      //用户数据
      userInfo:[],

      // 我的订单nav数据
      navList:[
        {
          title:"待付款",
          icon:"iconfont iconqianbao",
          url:"order",
        },
        {
          title:"待发货",
          icon:"iconfont icondaifahuo",
          url:"order"
        },
        {
          title:"待收货",
          icon:"iconfont icondeliver_fill",
          url:"order"
        },
        {
          title:"待评价",
          icon:"iconfont iconliuyanfill",
          url:"order"
        },
        {
          title:"售后/退款",
          icon:"iconfont icontuikuan",
          url:"order"
        },
      ],

      // 我的服务nav数据
      myList:[
        {
          title:"我的余额",
          icon:"gold-coin",
          url:"/user"
        },
        {
          title:"我的收藏",
          icon:"goods-collect",
          url:"/collection"
        },
        {
          title:"地址列表",
          icon:"map-marked",
          url:"/addressList"
        },
      ]
    }
  },
  created(){
    this.getUserData()
  },
  methods:{
    // 获取用户信息
    getUserData(){
      //获取token
      let tokenString = this.$cookies.get("tokenString");

      //判断token存不存在
      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method:"GET",
        url:this.baseUrl+"/user",
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      }).then((res) => {
          console.log("res getAddressListData ==> ", res);
          this.$toast.clear();
          
          if (res.data.status == 200) {
            this.userInfo = res.data.data.switchUserInfo[0]

            this.navList[0].count = res.data.data.orderStatusNum.unpaid_count
            this.navList[1].count = res.data.data.orderStatusNum.unshipped_count
            this.navList[2].count = res.data.data.orderStatusNum.received_count
            this.navList[3].count = res.data.data.orderStatusNum.evaluated_count
            this.navList[4].count = res.data.data.orderStatusNum.refund_count

          }else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            this.$router.push("/login")
          }
      }).catch((err) => {
        this.$toast.clear();
        console.log("err ==> ", err);
      });
    },

    //去到设置
    goInfo(){
      this.$router.push("/personalInfo")
    }

  }
}
</script>